/* ========== Í≥µÌÜµ Î†àÏù¥ÏïÑÏõÉ ========== */
:root {
    --vh: 100%;
}




.home {
    width: 100%;
    min-height: 100vh;
    background: #ffffff;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    /* ÌôîÎ©¥ Ï†ÑÏ≤¥ ÍΩâ Ï±ÑÏö∞Í∏∞ */
    max-height: 900px;
    /* ÎÑàÎ¨¥ Í∏∏Ïñ¥ÏßÄÏßÄ ÏïäÍ≤å Ï†úÌïú */
    overflow-y: auto;
    /* ÌïÑÏöîÌïòÎ©¥Îßå Ïä§ÌÅ¨Î°§ */
}

/* Ìó§Îçî */
.home__header {
    position: sticky;
    top: 0;
    z-index: 20;
    display: flex;
    justify-content: flex-start;
    /* ‚úÖ ÏôºÏ™Ω Í∏∞Ï§Ä Ï†ïÎ†¨ */
    align-items: flex-end;
    gap: 12px;
    padding: 0 16px 20px;
    background: #4A6E45;
    color: #fff;
    height: 60px;
}

.home__logo {
    align-self: center !important;
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: "Inter", system-ui, -apple-system, "Segoe UI", Arial;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    flex-shrink: 0;
    top: 15px;
    display: block !important;

}

.home__logo-img {

    width: 100px !important;
    height: 100px !important;
    top: 100 !important;
    object-fit: contain;
    filter: brightness(100%) contrast(110%);
}

/* üî• ÌïµÏã¨ Î∂ÄÎ∂Ñ */
.home__search {
    margin-left: 50px;
    /* ‚úÖ Ïò§Î•∏Ï™Ω ÎÅùÏúºÎ°ú Î∞ÄÍ∏∞ */
    display: flex;
    align-items: center;
    /* ‚úÖ ÏõêÌïòÎäî ÎßåÌÅº Ï§ÑÏù¥Í∏∞ (120‚Üí140‚Üí100 ÎßàÏùåÎåÄÎ°ú Ï°∞Ï†à Í∞ÄÎä•) */
    gap: 6px;
    background: #fff;
    padding: 5px 10px;
    border-radius: 8px;
}


.home__search input {
    border: none;
    outline: none;
    font-size: 14px;
    width: 100%;
}

.home__searchBtn {
    border: none;
    background: transparent;
    font-size: 16px;
    cursor: pointer;
}

/* ========== ÏßÄÎèÑ ÏòÅÏó≠ ========== */
.map {
    position: relative;
    width: 100%;
    /* ÏïÑÏù¥Ìè∞ ÌôîÎ©¥ ÎπÑÏä∑Ìïú ÎπÑÏú®Î°ú ÎÜíÏù¥ ÏÑ§Ï†ï */
    height: 920px;
    overflow: hidden;
}

/* ÌöåÏÉâ Î∞îÎã• Î∞∞Í≤Ω */
.map__bg {
    position: absolute;
    inset: 0;
    background: #cbd6dd;
}

/* Ï§ëÏïô ÏõêÌòï(Í¥ëÏû• ÎäêÎÇå) */
.map__circle {
    position: absolute;
    top: 130px;
    left: 70%;
    width: 80px;
    height: 80px;
    margin-left: -70px;
    background: #dfe3e8;
    border-radius: 50%;
    opacity: 0.85;
}

.jacob-ladder {
    position: absolute;
    width: 60px;
    /* ÌÅ¨Í∏∞ Ï°∞Ï†à Í∞ÄÎä• */
    height: 60px;
    background: #d9dee2;
    /* Í¥ëÏû• ÏõêÌòïÍ≥º ÎèôÏùºÌïú ÏÉâÏÉÅ */
    border-radius: 6px;
    /* ÏÇ¥Ïßù Îë•Í∑º ÏßÅÏÇ¨Í∞ÅÌòï */
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: 12px;
    color: #767c84;
    /* Îã§Î•∏ Í±¥Î¨º Í∏ÄÏî®Îûë ÎπÑÏä∑ÌïòÍ≤å */
    left: 310px;
    /* ÏúÑÏπòÎäî ÏòàÏãú (Î∞ÄÏïåÍ¥Ä Î∞ëÏúºÎ°ú Ï°∞Ï†ï) */
    top: 250px;
}


/* Ï¢åÌïòÎã® Ï¥àÎ°ù Î∏îÎ°ùÎì§ (ÏûîÎîî/Í¥ëÏû•) */
.map__field {
    position: absolute;
    background: #a8c9b1;
    border-radius: 12px;
    opacity: 0.9;
}

.map__field--small {
    top: 450px;
    left: 60px;
    width: 60px;
    height: 90px;

}

.map__field--large {
    top: 430px;
    left: 120px;
    width: 120px;
    height: 75px;
    transform: rotate(12deg);
}

/* Ïö∞ÌïòÎã® Í≥°ÏÑ† Î≥¥ÎèÑ */
.map__curb {
    position: absolute;
    bottom: 45px;
    top: 600px;
    left: 80px;
    width: 150px;
    height: 120px;
    background: #dfe3e8;
    border-bottom-left-radius: 100px 120px;
    border-top-left-radius: 50px 40px;
    transform: rotate(10deg);
    opacity: 0.8;
}

/* Ïö∞Ï∏° ÏñáÏùÄ Í∏∞Îë•(Ïû•Ïãù) */
.map__thin-rail {
    position: absolute;
    top: 170px;
    right: 16px;
    width: 10px;
    height: 150px;
    background: #e9eef3;
    border-radius: 8px;
    opacity: 0.9;
    margin-top: -90px;
}

/* Ï£ºÏ∞® P */
.map__parking {
    position: absolute;
    top: 160px;
    left: 20px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #b7c5d9;
    color: #fff;
    font-weight: 700;
    display: grid;
    place-items: center;
}

/* Î¨∏ ÌÖçÏä§Ìä∏ */
.map__gate {
    position: absolute;
    color: #ffffff;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.45);
    font-weight: 800;
    letter-spacing: 0.5px;
    margin-top: -100px;
}

.map__gate-box {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;

    width: 60px;
    /* ÌÅ¨Í∏∞ */
    height: 30px;
    border-radius: 6px;

    background: #8B9FBA;
    /* Î¨∏ ÏÉâÏÉÅ */
    color: #fff;
    font-weight: 450;

    font-size: 14px;
    text-align: center;
}

.map__gate--front {
    top: 0px;
    left: 95px;
    border-radius: 0 0 15px 15px;
}

.map__gate--back {
    bottom: 200px;
    left: 0px;
    border-radius: 0 15px 15px 0px;
}

/* ========== Í±¥Î¨º Í≥µÌÜµ Ïä§ÌÉÄÏùº ========== */
.bld {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #eef3f8;
    color: #7b8494;
    font-weight: 700;
    border-radius: 14px;
    text-decoration: none;
    box-shadow: 0 1.5px 0 rgba(0, 0, 0, 0.05), inset 0 0 0 1px #d7dde5;
    transition: transform 0.12s ease, box-shadow 0.12s ease;
    user-select: none;
}

.bld:hover {
    background: #8B9FBA;
    color: #fff;
}



/* ========== Í∞úÎ≥Ñ Í±¥Î¨º ÏúÑÏπò/ÌÅ¨Í∏∞/Í∞ÅÎèÑ ========== */
/* Î≥µÏùåÍ¥Ä (ÏÉÅÎã® Ï§ëÏïô Í∞ÄÎ°úÎ°ú Í∏¥ Î∏îÎ°ù) */
.bld--bogeum {
    top: 180px;
    left: 180px !important;
    width: 180px !important;
    height: 60px !important;
    margin-top: -130px;
}

/* Î∞ÄÏïåÍ¥Ä (Ïö∞Ï∏° ÏÑ∏Î°úÎ°ú Í∏¥ Î∏îÎ°ù + Ïö∞ÏÉÅÎã® ÎùºÏö¥Îìú) */
.bld--milal {
    top: 160px;
    right: 10px;
    width: 70px;
    height: 180px !important;
    border-top-right-radius: 26px;
    border-bottom-right-radius: 18px;
    margin-top: -110px;
}

/* Í∞àÎ©úÍ¥Ä (Ï¢åÏ∏° ÏÉÅÎã® ÎåÄÍ∞ÅÏÑ†) */
.bld--galmel {
    top: 160px !important;
    left: 10px !important;
    width: 170px !important;
    height: 60px;
    transform: rotate(-75deg);


}


/* Î™®Î¶¨ÏïÑÍ¥Ä (ÌôîÎ©¥ Ï§ëÎã® ÎåÄÍ∞ÅÏÑ†, Í∏∏Ï≠â) */
.bld--Moria {
    top: 330px !important;
    left: 80px !important;
    width: 230px !important;
    height: 68px !important;
    transform: rotate(12deg);
}

/* ÏùºÎ¶ΩÍ¥ÄA (Ïö∞ÌïòÎã® ÎåÄÍ∞ÅÏÑ†, ÏúÑÏ™Ω) */
.bld--illipA {
    top: 460px !important;
