}

/* ÏùºÎ¶ΩÍ¥ÄB (Ïö∞ÌïòÎã® ÎåÄÍ∞ÅÏÑ†, ÏïÑÎûòÏ™Ω) */
.bld--illipB {
    top: 594px !important;
    right: 120px !important;
    width: 92px;
    height: 155px;
    transform: rotate(28deg);
}

/* Î∞òÏùëÌòï(ÎÑìÏùÄ ÌôîÎ©¥ÏóêÏÑú ÏÇ¥Ïßù ÌÇ§Ïö∞Í∏∞) */
@media (min-width: 430px) {
