/* Í∏∞Ï°¥ ÏΩîÎìú Îß® ÏúÑÏùò .chatbot-button ÍµêÏ≤¥ */
.chatbot-button {
  position: fixed;            /* ‚úÖ ÌôîÎ©¥ Í≥†Ï†ï */
  bottom: 24px;               /* ‚úÖ ÌïòÎã® Ïó¨Î∞± */
  right: 24px;                /* ‚úÖ Ïò§Î•∏Ï™Ω Ïó¨Î∞± */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: none;
  background: #2f5632;        /* Ïú†ÎãàÎÇòÎπÑ Í∑∏Î¶∞ Í≥ÑÏó¥ */
  color: #fff;
  font-size: 26px;
  cursor: pointer;
  z-index: 9999;              /* ‚úÖ Ìï≠ÏÉÅ Îß® ÏúÑÏóê */
  box-shadow: 0 4px 10px rgba(0,0,0,0.2);
  transition: transform 0.2s ease;
}
.chatbot-button:hover {
  transform: scale(1.08);
}
/* Ìå®ÎÑê */
.chatbot-wrap {
  position: fixed;
  right: 12px;
  bottom: 12px;
  width: min(420px, 92vw);
  height: min(520px, 70vh);
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 12px 40px rgba(0,0,0,0.18);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  z-index: 9999;
}

/* Ìó§Îçî */
.chatbot-header {
  height: 48px;
  background: #2f5632; /* Ïú†ÎãàÎÇòÎπÑ Ìó§Îçî ÌÜ§Í≥º Ïú†ÏÇ¨Ìïú Îî•Í∑∏Î¶∞ */
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 12px 0 14px;
}
.chatbot-title { font-weight: 600; }
.chatbot-close {
  background: transparent; color: #fff;
  border: none; font-size: 22px; line-height: 1;
  cursor: pointer;
}

/* Î≥∏Î¨∏(Ïä§ÌÅ¨Î°§ ÏòÅÏó≠) */
.chatbot-body {
  flex: 1;
  padding: 12px;
  overflow: auto;
  background: #f6f8f7;
}

/* ÎßêÌíçÏÑ† */
.row { display: flex; margin: 6px 0; }
.row.user { justify-content: flex-end; }
.row.bot  { justify-content: flex-start; }
.bubble {
  max-width: 80%;
  padding: 10px 12px;
  border-radius: 12px;
  font-size: 14px;
  line-height: 1.4;
  box-shadow: 0 1px 2px rgba(0,0,0,0.08);
}
.bubble-user {
  background: #e7f1e7;
  border-top-right-radius: 4px;
}
.bubble-bot {
  background: #ffffff;
  border-top-left-radius: 4px;
  white-space: pre-line;  
}

/* ÌïòÎã® ÏûÖÎ†• */
.chatbot-footer {
  display: flex;
  gap: 8px;
  padding: 10px;
  border-top: 1px solid #e9eceb;
  background: #fff;
}
.chatbot-input {
  flex: 1;
  height: 38px;
  padding: 0 10px;
  border: 1px solid #d8dedb;
  border-radius: 8px;
  outline: none;
}
.chatbot-input:disabled { background: #f2f4f3; }
.chatbot-send {
  height: 38px;
  padding: 0 14px;
  border: none;
  border-radius: 8px;
  background: #ccd7cc;
  color: #667266;
}
.chatbot-send:disabled { opacity: 0.9; }

/* ÏûëÏùÄ ÌôîÎ©¥ÏóêÏÑú ÍΩâ Ï∞®Í≤å Î≥¥Ïù¥ÎèÑÎ°ù ÏòµÏÖò */
@media (max-width: 420px) {
  .chatbot-wrap {
    right: 8px; left: 8px;
    width: auto; height: min(70vh, 520px);
  }
}